
.sectionViewer {
  text-align: left;
  color: black;
}
.hSection {
  background: #fafafa;
  margin: 1rem;
  padding: 1rem;
}
.document .light {
  color: #555;
  font-weight: 300;
}
.document .article {
  padding: 1rem;
  margin-bottom: 0;
  transition: 100ms background;
}
.document .article:hover {
  background: white;
}
.document h2 {
  margin: 0;
  margin-top: 16px;
  font-size: 20px;
}
.document p {
  margin-top: 8px;
}
.document a {
  text-decoration: none;
  color: black;
  border-bottom: 1px dotted;
  font-weight: bold;
}



